* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.content {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  text-align: center;
  font-family: "Montserrat", sans-serif;

  @media screen and (max-width: 576px) {
    position: relative;
  }

  .infoBox {
    height: 80%;
    width: 100%;
    padding-top: 10%;
    div > h1 {
      font-size: 70px;
      font-family: "Russo One", sans-serif;
      font-weight: normal;
      @media screen and (max-width: 576px) {
        margin-top: 30px;
        font-size: 35px;
        padding-bottom: 20px;
      }
    }
    div > h2 {
      font-size: 30px;
      margin-bottom: 50px;
      font-family: "Russo One", sans-serif;
      font-weight: normal;
      @media screen and (max-width: 576px) {
        font-size: 20px;
      }
    }
    div > p {
      width: 50%;
      padding-bottom: 20px;
      margin: 0 auto;
      color: grey;
      font-size: 18px;
      line-height: 35px;
      @media screen and (max-width: 576px) {
        font-size: 15px;
        line-height: 25px;
        width: 80%;
        padding-bottom: 20px;
      }
    }
    .separate {
      width: 400px;
      height: 1px;
      background: rgb(255, 255, 255);
      margin: 0 auto;
      @media screen and (max-width: 576px) {
        width: 80%;
        margin-top: 40px;
      }
    }
    .developer {
      color: #46a4d7;
    }
    .social {
      width: 30%;
      display: flex;
      margin: 0 auto;
      justify-content: center;
      font-size: 30px;
      padding-top: 25px;

      .social-icon {
        margin: 0 30px;
        transition: 0.5s;
        &:hover {
          color: #46a4d7;
        }
      }

      a {
        color: white;
      }
    }
  }

  .down {
    @media screen and (max-width: 576px) {
      font-size: 15px;
      line-height: 25px;
      width: 80%;
      padding-bottom: 20px;
    }
    a {
      font-size: 35px;
      color: white;
      transition: 0.5s;

      &:hover {
        font-size: 40px;
        cursor: pointer;
      }
    }
  }
}
